import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'

import Index from './views/index'
import './style.css'

Vue.use(Router)
Vue.use(Meta)
export default new Router({
  mode: 'history',
  routes: [
    {
      name: 'index',
      path: '/',
      component: Index,
    },
  ],
})
